import React from 'react';
import {CSVLink} from 'react-csv'
import resultStore from "../../store/resultStore";

const ExportXls = () => {
    const dataSet = resultStore.resultData
    const headers = [
        {label: '', key: 'day'},
        {label: ' ', key: 'profit'},
        {label: '    ', key: 'need'},
        {label: '    ', key: 'depo'}
    ]

    return (
        <button style={{cursor: 'pointer'}}>
            <CSVLink style={{textDecoration:'none'}} data={dataSet} headers={headers} filename='DepositCalculation'> CSV</CSVLink>
        </button>
    );
};

export default ExportXls;